import React, { useState } from 'react';
import { navigate } from 'gatsby';

import { EuiIcon, EuiSideNav, EuiSpacer, EuiTitle } from '@elastic/eui';

type Props = {
  navItem?: string;
};

export function ObservabilityNav({ navItem }: Props) {
  const [isSideNavOpenOnMobile, setIsSideNavOpenOnMobile] = useState(false);

  const toggleOpenOnMobile = () => {
    setIsSideNavOpenOnMobile(!isSideNavOpenOnMobile);
  };

  const createItem = (name: string, data = {}) => {
    return {
      id: name,
      name,
      // @ts-ignore
      isSelected: data.url === navItem || name === navItem,
      // @ts-ignore
      disabled: !data.url,
      // @ts-ignore
      onClick: data.url
        ? () => {
            // @ts-ignore
            navigate(data.url);
          }
        : () => {
            return null;
          },
      ...data,
    };
  };

  const sideNav = [
    createItem('Overview', { url: 'observability/overview' }),
    createItem('Logs', {
      items: [
        createItem('Stream', { url: '/observability/stream' }),
        createItem('Anomalies'),
        createItem('Categories'),
      ],
    }),
    createItem('Infrastructure', {
      items: [
        createItem('Inventory', { url: '/observability/inventory' }),
        createItem('Metrics explorer'),
      ],
    }),
    createItem('APM', {
      items: [
        createItem('Services', { url: '/observability/services' }),
        createItem('Service map'),
        createItem('Traces'),
      ],
    }),
    createItem('Uptime', {
      items: [
        createItem('Monitoring overview', { url: '/observability/uptime' }),
        createItem('Certificates'),
      ],
    }),
    createItem('User experience', { 
      items: [
        createItem('Dashboard', {url: '/observability/user-experience' }),
        createItem('Performance Analyzer'),
      ]
    }),
    // createItem('[K] Analytics', { 
    //   items: [
    //     createItem('Alerts'),
    //     createItem('Dashboards'),
    //   ]
    // }),
    // createItem('Settings', { url: 'observability/settings' }),
  ];

  return (
    <>
      <EuiTitle size="xs">
        <h2>
          <EuiIcon size="l" type="logoObservability" /> &ensp; Observability
        </h2>
      </EuiTitle>
      <EuiSpacer />
      <EuiSideNav
        mobileTitle="Navigate within Observability"
        toggleOpenOnMobile={toggleOpenOnMobile}
        isOpenOnMobile={isSideNavOpenOnMobile}
        items={sideNav}
      />
    </>
  );
}
